*,
*:before,
*:after {
	box-sizing: border-box;
}

html {
	scroll-behavior   : smooth;
	scroll-padding-top: 8rem;

	font-family: sans-serif;
	line-height: 1.75;
}

header,
main {
	margin-inline: auto;
	width : min(calc(100% - 2vw), 48rem);
}

header {
	position  : sticky;
	top       : 0;
	z-index   : 1;
	min-height: 6rem;

	border          : 1px solid #ddd;
	background-color: rgba(#fff, 0.5);
	backdrop-filter : blur(4px);
}

main {
	margin-top: 2rem;
}

div + div {
	margin-top: 1rem;
}

.layout-wrap {
	display  : flex;
	flex-wrap: wrap;
	gap      : 0.5rem;
}
